<template>
  <div>
    <v-card light class="mb-8">
      <v-toolbar class="primary white--text">
        <v-card-title>
          {{ $t('sidebar.newsletter.heading') }}
        </v-card-title>
      </v-toolbar>
      <v-card-text>
        <base-alert display="sidebar-newsletter" />
        <p class="text-left">{{ $t('sidebar.newsletter.text') }}</p>
        <v-form
          ref="subscriberSidebarForm"
          v-model="valid"
          @submit.prevent="save"
        >
          <v-text-field
            v-model="item.email"
            rounded
            prepend-inner-icon="mdi-magnify"
            outlined
            dense
            single-line
            :label="$t('sidebar.newsletter.subscribeInput.label')"
            required
            :rules="[rules.required, rules.isEmail]"
          />
          <div class="text-center">
            <v-btn
              :loading="$store.state.loading.newsletter"
              rounded
              class="primary white--text mt-3"
              type="submit"
            >
              <v-icon class="mr-2">mdi-email</v-icon>
              {{ $t('sidebar.newsletter.button.text') }}
            </v-btn>
          </div>
        </v-form>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import validations from '@/mixins/validations'
export default {
  mixins: [validations],
  data() {
    return {
      item: { email: '' }
    }
  },
  methods: {
    ...mapActions('newsletter', ['addSubscriber']),
    async save() {
      this.$refs.subscriberSidebarForm.validate()
      if (this.valid) {
        const payload = {
          subscriber: this.item,
          axios: this.$axios,
          form: this.$refs.subscriberSidebarForm,
          alertDisplay: 'sidebar-newsletter'
        }
        await this.addSubscriber(payload)
      }
    }
  }
}
</script>

<style lang="scss" scoped></style>
